@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}*,*::after,*::before{box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%;font-weight:300;line-height:1.6;background-color:whitesmoke}.container{display:grid;grid-template-columns:minmax(40rem, 60rem) minmax(30rem, 40rem);grid-template-rows:-webkit-min-content;grid-template-rows:min-content;margin:4rem;border-radius:2rem;justify-content:center}@media only screen and (max-width: 37.5em){.container{grid-template-columns:1fr}}

.choose-date{background-color:#00a1ab;color:white;display:flex;flex-direction:column;align-items:center;padding:1rem;border-radius:0 1rem 1rem 0;grid-column:2/-1;grid-row:1/1}@media only screen and (max-width: 37.5em){.choose-date{grid-column:1/1;grid-row:2/2}}.choose-date h1{font-size:3rem;-webkit-animation:slots .5s ease;animation:slots .5s ease}.choose-date__nothing{font-size:3rem}.sessions{display:flex;align-items:center;flex-direction:row;transition:all 2s ease}.sessions__hour{color:white;font-size:2rem;text-decoration:none;list-style:none;transition:all .5s;background-color:#00263b;padding:0.5rem 1.5rem;margin:1rem;font-weight:700;cursor:pointer;-webkit-animation:slots .5s ease;animation:slots .5s ease;text-align:center}.sessions__hour:hover,.sessions__hour:focus{background-color:#ff5200;transform:translateY(-3px);color:#00263b;font-weight:700;box-shadow:0 1rem 1rem rgba(0,0,0,0.2)}.sessions__hour.active{background-color:#ff5200;transform:scale(1.1);color:#00263b;font-weight:700;box-shadow:0 0.5rem 0.5rem rgba(0,0,0,0.2)}@-webkit-keyframes slots{0%{transform:translateY(30px);opacity:0}100%{transform:translateY(0);opacity:1}}@keyframes slots{0%{transform:translateY(30px);opacity:0}100%{transform:translateY(0);opacity:1}}

.popup{background-color:#00a1ab;grid-column:2/3;position:relative;margin-top:3rem;padding:1rem;border-radius:0 1rem 1rem 0;color:white;align-self:flex-start;width:100%}.popup__close:link,.popup__close:visited{color:white;position:absolute;top:-2rem;right:0;font-size:3rem;text-decoration:none;display:inline-block;transition:all .2s;line-height:1}.popup__close:hover{color:#ff5200;transform:scale(1.6)}.form{display:flex;flex-direction:column}.form__group{position:relative;padding:15px 0 0;margin-top:10px}.form__group:not(:last-child){margin-bottom:2rem}.form__input{font-family:inherit;width:100%;border:0;border-bottom:2px solid #00263b;outline:0;font-size:1.3rem;color:#fff;padding:7px 0;background:transparent;transition:border-color 0.2s}.form__input--textarea{outline:none;background-color:#00b9c5;border:none;font-family:inherit;width:100%;color:#fff;resize:none}.form__input:focus:invalid{border-bottom:3px solid #6f0000}.form__input::-webkit-input-placeholder{color:transparent}.form__input:-ms-input-placeholder{color:transparent}.form__input::-ms-input-placeholder{color:transparent}.form__input::placeholder{color:transparent}.form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#fff;font-weight:400;padding-left:1rem}.form__input:placeholder-shown+.form__label{font-size:1.4rem;cursor:text;top:2rem}.form__input:focus{padding-bottom:6px;border-bottom:3px solid #ff5200}.form__input:focus ~ .form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#fff;font-weight:400;padding-left:0rem}.form__btn{margin:0 auto;display:block;color:white;font-size:2rem;text-decoration:none;list-style:none;transition:all .5s;background-color:#ff5200;padding:0.5rem 1.5rem;font-weight:700;cursor:pointer;-webkit-animation:slots .5s ease;animation:slots .5s ease;text-align:center;border:none}.form__btn:hover,.form__btn:focus{background-color:#ff5200;transform:translateY(-3px);color:#00263b;font-weight:700;box-shadow:0 1rem 1rem rgba(0,0,0,0.2)}

.background{grid-column:2/-1;grid-row:1/1;position:relative;overflow:hidden;z-index:-1;transition:all 2s ease}.background--active{z-index:1000;display:inline-block}.background--active .background__msg{transform:scale(50)}.background--disabled{z-index:-1;opacity:0}.background--active .background__text{display:inline-block;width:100%;text-align:center}.background__msg{height:6rem;width:6rem;border-radius:50%;position:absolute;top:0rem;right:0rem;background-color:#ff5200;transition:transform 0.8s cubic-bezier(0.86, 0, 0.07, 1)}.background__msg--active{transform:scale(50)}.background__text{color:white;z-index:1001;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);-webkit-animation:2s showUp ease;animation:2s showUp ease;display:none}.background__close:link,.background__close:visited{color:white;position:absolute;z-index:1002;top:2.5rem;right:2.5rem;font-size:6rem;text-decoration:none;display:inline-block;transition:all .2s;line-height:1}.background__close:hover{color:#00263b;transform:scale(1.6)}@-webkit-keyframes showUp{0%{opacity:0;top:0;left:0;transform:translate(0, 0)}100%{top:50%;left:50%;transform:translate(-50%, -50%);opacity:1}}@keyframes showUp{0%{opacity:0;top:0;left:0;transform:translate(0, 0)}100%{top:50%;left:50%;transform:translate(-50%, -50%);opacity:1}}

.icon{font-family:'Material Icons', serif;font-style:normal;display:inline-block;vertical-align:middle;line-height:1;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;direction:ltr;-webkit-font-smoothing:antialiased;text-rendering:optimizeLegibility;-moz-osx-font-smoothing:grayscale;font-feature-settings:'liga'}.row{margin:0;padding:0;display:grid;grid-template-columns:repeat(7, 1fr)}.calendar{display:grid;position:relative;width:100%;background:white;border:1px solid lightgray;height:100%;margin:0 auto;border-radius:1rem 0 0 1rem;grid-template-columns:repeat(7, 1fr);grid-template-rows:-webkit-min-content -webkit-min-content -webkit-min-content;grid-template-rows:min-content min-content min-content}.calendar .header{text-transform:uppercase;text-align:center;color:#00263b;font-weight:700;font-size:1.8rem;padding:1.2em 0;border-bottom:1px solid lightgray;grid-column:1/-1;grid-row:1/1;display:flex;justify-content:center}.calendar .header .icon{cursor:pointer;transition:.15s ease-out}.calendar .header .icon:hover{transform:scale(1.75);transition:.25s ease-out;color:#ff5200}.calendar .header .icon:first-of-type{margin-left:1em}.calendar .header .icon:last-of-type{margin-right:1em}.calendar .days{grid-column:1/-1;display:grid;grid-template-columns:repeat(7, 1fr);text-transform:uppercase;font-weight:700;color:#00263b;font-size:1.5rem;padding:1rem 0;text-align:center}.calendar .body{grid-column:1/-1;display:grid;grid-template-columns:1fr;grid-row:3/4;grid-auto-flow:row}.calendar .body .cell{position:relative;display:flex;justify-content:center;align-items:center;flex-direction:column;padding:2.5rem;color:#00263b;overflow:hidden;cursor:pointer;opacity:0.9;border-radius:50%;transition:2s ease-out}.calendar .body .cell:hover{transform:translateY(-3px);transition:0.5s ease-out}.calendar .body .cell:last-child{border-right:none}.calendar .body .cell .number{font-size:1.5rem;line-height:1;font-weight:700}.calendar .body .cell .today{position:absolute;display:inline-block;height:50%;width:50%;border:1px solid #00a1ab;border-radius:50%;z-index:-6}.calendar .body .session__containter{justify-self:flex-end;display:flex;margin-top:0.4rem}.calendar .body .drsession{background-color:#ff5200;width:.5rem;height:.5rem;border-radius:50%}.calendar .body .drsession:not(:last-child){margin-right:3px}.calendar .body .drsession--booked{background-color:gainsboro;width:.5rem;height:.5rem;border-radius:50%}.calendar .body .drsession--booked:not(:last-child){margin-right:3px}.calendar .body .drsession-full{position:absolute;background-color:purple;display:block;width:.5rem;height:.5rem;border-radius:50%;top:70%;left:45%}.calendar .body .selected{transition:all 2s;color:white;z-index:2;position:relative}.calendar .body .selected::before{content:'';position:absolute;display:inline-block;height:60%;width:60%;background-color:#00263b;border-radius:50%;z-index:-1;box-shadow:0 0.5rem 1rem rgba(0,0,0,0.2);-webkit-animation:showCricle 0.2s cubic-bezier(0.14, 1.98, 0.44, 0.53);animation:showCricle 0.2s cubic-bezier(0.14, 1.98, 0.44, 0.53)}.calendar .body .selected .number{text-align:center}.calendar .body .disabled{color:lightgray;pointer-events:none}@-webkit-keyframes showCricle{0%{transform:scale(0)}100%{transform:scale(1)}}@keyframes showCricle{0%{transform:scale(0)}100%{transform:scale(1)}}

